.clock {
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.clock h4 {
  margin: 0;
  font-size: 10px;
  font-weight: lighter;
}

.clock h4 span {
  font-weight: bold;
  color: lightcoral;
}

.clock h4 span:hover {
  text-decoration: underline;
  cursor: pointer;
  color: red;
}
